___CSS_LOADER_EXPORT___.push([module.id, "*[data-v-70166988]{color:#fff;text-shadow:1px 1px 1px #000,2px 2px 2px #00008b}.narrow[data-v-70166988]{width:50%}", "",{"version":3,"sources":["webpack://./resources/js/views/footer/company.vue"],"names":[],"mappings":"AAgCA,mBACI,UAAY,CACZ,gDACJ,CAEA,yBACI,SACJ","sourcesContent":["<template>\n    <h4> Tietoevry Lithuania</h4>\n    <br>\n    <h5> Lvovo g. 105A, Vilnius 08104, Lithuania</h5>\n    <br>\n    <h5>A leading digital services and software company</h5>\n    <br>\n    <h6 class=\"narrow\">\n        Tietoevry creates digital advantage for businesses and society. We are a leading digital services and software\n        company with local presence and global capabilities. Our Nordic values and heritage steer our success.\n        Headquartered in Finland, Tietoevry employs around 24 000 experts globally. The company serves thousands of\n        enterprise and public sector customers in more than 90 countries. Tietoevrys annual turnover is approximately\n        EUR 3 billion and its shares are listed on the NASDAQ in Helsinki and Stockholm as well as on the Oslo Brs.\n    </h6>\n    <br>\n    <h5>What we do at work</h5>\n    <br>\n    <h6 class=\"narrow\">\n        Did you know that you deal with us almost every day? When you withdraw money at an ATM, when you buy a train\n        ticket online, make mobile payments or when you receive a digital message that it is time to pick up a package\n        you have ordered. You have probably used our solutions more than once. The team behind the scenes for these\n        solutions is a large group of strategists, coders, analysts, industry experts and future enthusiasts.\n    </h6>\n</template>\n\n<script>\nexport default {\n    name: \"company\"\n}\n</script>\n\n<style scoped>\n* {\n    color: white;\n    text-shadow: 1px 1px 1px #000, 2px 2px 2px darkblue;\n}\n\n.narrow {\n    width: 50%;\n}\n</style>\n"],"sourceRoot":""}]);
